@charset "UTF-8";
@import "../../../style/theme/default/var.css";
/*互动数据图表模块*/
.interactive-panel{
  border:1px solid var(--theme-border-color-primary);
  padding: var(--theme-padding-base) var(--theme-padding-base) 25px;

  &.panel-rt {
    min-height: 425px;
    margin-bottom: 10px;
  }

  &.panel-rb{
    height: 234px;
    overflow: hidden;
  }

}

.interactive-panel p {
  font-size: var(--theme-font-size-base);
  margin-bottom:0;
  &.text-title {
     font-size:  var(--theme-font-size-middle);
   & > i {
      color: var(--theme-color-primary);
      font-size: 24px;
      padding: 0 5px;
      vertical-align: middle;
  }
 }
}
.interactive-content {
  text-align: center;
  padding-top: 48px;
}

.unit-main {
  width: 512px;
  margin: 0 auto;
  position: relative;
}

.interactive-content .unit-main:first-child .main-right{
  border-bottom-color: transparent;
}
.interactive-content .unit-main:first-child .main-right:after{
  content:normal;
}
.interactive-content .unit-main:first-child .main-left:before {
  display: none;
}


.interactive-content .unit-main:last-child {
  .main-left {
    &:after {
      display: none;
    }
  }
  .unit-list:before {
    display: none;
  }
}


.interactive-content .unit-part {
  width: 320px;
  margin: 0 auto;
  height: 110px;
  display: inline-block;
  vertical-align: bottom;
}

.interactive-content .unit-list {
  width: 320px;
  float: right;
  display: inline-block;
  height: 70px;
  background: #2f323a;
  color: #fff;
}

.interactive-content .unit-list .icon {
  font-size: 12px !important;
  padding-left: 5px;
  cursor: pointer;
}

.interactive-content .unit-left, .unit-right {
  width: 50%;
  float: left;
  text-align: center;
  margin-top: 15.8px;
}

.interactive-content .unit-left, .unit-right, .unit-one {
   & > span:first-child {
      margin-bottom: 5px;
    }
}

.interactive-content .unit-left span:first-child {
  display: block;
}

.interactive-content .unit-right span:first-child {
  display: block;
}

.interactive-content .unit-one {
  position: relative;
  top: 15.8px;
}

.interactive-content .unit-one span {
  display: block;
}

.interactive-content .unit-shadow {
  width: 290px;
  float: right;
  display: inline-block;
  height: 40px;
  background: #c3cdd9;
  position: relative;
}

.interactive-content .unit-shadow.level-1:before {
  content: '';
  display: block;
  width: 0;
  height: 0;
  border-top: 41px solid #fff;
  border-right: 100px solid transparent;
  position: relative;
  left: 0;
  top: 0;
}

.interactive-content .unit-shadow.level-1:after {
  content: '';
  display: block;
  width: 0;
  height: 0;
  border-bottom: 41px solid #fff;
  border-left: 100px solid transparent;
  position: absolute;
  right: 0;
  top: 0;
}

.interactive-content .unit-shadow:after {
  content: '';
  display: block;
  width: 0;
  height: 0;
  border-bottom: 41px solid #fff;
  border-left: 70px solid transparent;
  position: absolute;
  right: 0;
  top: 0;
}

.interactive-content .unit-shadow:before {
  content: '';
  display: block;
  width: 0;
  height: 0;
  border-top: 40px solid #fff;
  border-right: 70px solid transparent;
  position: relative;
  left: 0;
  top: 0;
}

.interactive-content .unit-shadow.level-4:after {
  content: '';
  display: block;
  width: 0;
  height: 0;
  border-bottom: 41px solid #fff;
  border-left: 50px solid transparent;
  position: absolute;
  right: 0;
  top: 0;
}

.interactive-content .unit-shadow.level-4:before {
  content: '';
  display: block;
  width: 0;
  height: 0;
  border-top: 40px solid #fff;
  border-right: 50px solid transparent;
  position: relative;
  left: 0;
  top: 0;
}

.interactive-content .unit-part.level-2 {
  width: 260px;
}

.interactive-content .unit-list.level-2 {
  width: 260px;
  background: #676d7e;
}

.interactive-content .unit-shadow.level-2 {
  width: 235px;
}

.interactive-content .unit-part.level-3 {
  width: 210px;
}

.interactive-content .unit-list.level-3 {
  width: 210px;
  background: #288acb;
}

.interactive-content .unit-shadow.level-3 {
  width: 180px;
}

.interactive-content .unit-part.level-4 {
  width: 150px;
}

.interactive-content .unit-list.level-4 {
  width: 150px;
  background: #ffae00;
}

.interactive-content .unit-shadow.level-4 {
  width: 130px;
}

.interactive-content .unit-part.level-5 {
  width: 110px;
}

.interactive-content .unit-list.level-5 {
  width: 110px;
  background: #e76c00;
}

.interactive-content .unit-list:before {
  content: '';
  display: block;
  width: 1px;
  height: 76px;
  border-left: 1px dashed #bcbcbc;
  position: absolute;
  left: 35.2999984px;
  top: 0;
}

.interactive-content .main-left:before, .main-left:after {
  content: '';
  display: block;
  position: absolute;
  left: 33px;
  width: 5px;
  height: 5px;
  background: #bcbcbc;
  border-radius: 100%;
}

.interactive-content .main-left:before {
  top: -37px;
}

.interactive-content .main-left:after {
  top: -3px;
}

.interactive-content .main-right:after {
  content: '';
  width: 0;
  height: 0;
  position: absolute;
  right: -14px;
  bottom: -4.5px;
  border-top: 4px solid transparent;
  border-right: 4px solid transparent;
  border-bottom: 4px solid transparent;
  border-left: 10px solid #eee;
}

.interactive-content .main-left, .main-right {
  display: inline-block;
  vertical-align: 86px;
}

.interactive-content .main-left {
  float: left;
  width: 71px;
  height: 35px;
  font-size: 16px;
  margin-top: -30px;
}

.interactive-content .main-right {
  width: 120px;
  border-bottom: 1px dashed #bcbcbc;
  margin-left: -3px;
  position: relative;
  top: 14.6px;
  font-size: 12px;
  line-height: normal;
  text-align: right;
  padding-right: 5px;
}

.interactive-content .main-right span {
  display: inline-block;
}

/*互动服务右边关怀模块*/
.list-area {
  display: block;
  width: 33.33%;
  float: left;
  padding: 15px 0;
  position:relative;
}

.list-area span {
  font-size: 12px;
  display: block;
  margin: 0 auto;
  text-align: center;
}
.list-area span.icon  {
  width: 40px;
  line-height: 40px;
  background: var(--theme-background-color-primary);
  border-radius: 100%;
  font-size: 20px;
  margin-bottom: 5px;
}

.list-area.chosed-unit {
  position: relative;
}

.list-area.chosed-unit span.icon {
  background: var(--theme-color-primary);
  color: var(--color-white);
}

.list-area label {
  display: block;
  position: absolute;
  top: 10px;
  left: 56%;
  width: 15px;
  height: 15px;
  font-size: 12px;
  color: var(--color-white);
  border-radius: 50%;
  line-height: 15px;
  background: var(--color-warning);
  text-align: center;
}




